    _initNav();
    _initCarousels();
    _initLoadMore();
    _initShareLinks();
    _initVideoPlayers();
    _initPageTransitions();
    _initNewsletterForm();
    _snapScrolling();
    _initBlogFilter();

    // Esc handlers
    $(document).keyup(function(e) {
      if (e.keyCode === 27) {

      }
    });

